import React, { useState, useEffect } from 'react';
import { Button } from '@/components/ui/button';
import { Dialog, DialogContent, DialogTitle } from '@/components/ui/dialog';
import { Input } from '@/components/ui/input';
import { Textarea } from '@/components/ui/textarea';
import { Select, SelectItem } from '@/components/ui/select';
import { Card, CardContent, CardHeader, CardTitle } from '@/components/ui/card';

export default function Ilanlarim() {
  const [ilanlar, setIlanlar] = useState([]);
  const [open, setOpen] = useState(false);
  const [newIlan, setNewIlan] = useState({
    kategori: '',
    baslik: '',
    aciklama: '',
    resim: '',
  });

  useEffect(() => {
    const storedIlanlar = JSON.parse(localStorage.getItem('ilanlar')) || [];
    setIlanlar(storedIlanlar);
  }, []);

  const handleSave = () => {
    const updatedIlanlar = [...ilanlar, newIlan];
    setIlanlar(updatedIlanlar);
    localStorage.setItem('ilanlar', JSON.stringify(updatedIlanlar));
    setNewIlan({ kategori: '', baslik: '', aciklama: '', resim: '' });
    setOpen(false);
  };

  return (
    <div className="p-6 max-w-3xl mx-auto">
      <h1 className="text-2xl font-bold mb-4">İlanlarım</h1>
      <Button onClick={() => setOpen(true)} className="mb-4">
        İlan Ekle
      </Button>
      <div className="grid grid-cols-1 gap-4">
        {ilanlar.map((ilan, index) => (
          <Card key={index}>
            <CardHeader>
              <CardTitle>{ilan.baslik}</CardTitle>
            </CardHeader>
            <CardContent>
              <p className="text-sm text-gray-600">Kategori: {ilan.kategori}</p>
              <p className="mt-2">{ilan.aciklama}</p>
              {ilan.resim && (
                <img
                  src={ilan.resim}
                  alt="İlan Resmi"
                  className="mt-2 w-full h-40 object-cover rounded-lg"
                />
              )}
            </CardContent>
          </Card>
        ))}
      </div>
      <Dialog open={open} onOpenChange={setOpen}>
        <DialogContent>
          <DialogTitle>Yeni İlan Ekle</DialogTitle>
          <Select
            onValueChange={(val) => setNewIlan({ ...newIlan, kategori: val })}
          >
            <SelectItem value="Araç">Araç</SelectItem>
            <SelectItem value="Emlak">Emlak</SelectItem>
            <SelectItem value="Elektronik">Elektronik</SelectItem>
          </Select>
          <Input
            placeholder="İlan Başlığı"
            value={newIlan.baslik}
            onChange={(e) => setNewIlan({ ...newIlan, baslik: e.target.value })}
          />
          <Textarea
            placeholder="Açıklama"
            value={newIlan.aciklama}
            onChange={(e) =>
              setNewIlan({ ...newIlan, aciklama: e.target.value })
            }
          />
          <Input
            type="text"
            placeholder="Resim URL"
            value={newIlan.resim}
            onChange={(e) => setNewIlan({ ...newIlan, resim: e.target.value })}
          />
          <Button onClick={handleSave}>Kaydet</Button>
        </DialogContent>
      </Dialog>
    </div>
  );
}
